
import { defineComponent, computed } from 'vue'
import { ElMessageBox, ElMessage } from 'element-plus'
import axios from 'axios'
import { useStore } from 'vuex'
import { useRouter } from 'vue-router'

export default defineComponent({
  name: 'Waiting',
  setup(props, { emit }) {
    const store = useStore()
    const router = useRouter()
    const participants = computed(() => store.state.participants)
    const meetingId = computed(() => store.state.meeting.id)

    const deleteMeeting = function () {
      axios.delete(`/meeting/${meetingId.value}`, 
        { headers: { Authorization: `Bearer ${localStorage.getItem("token")}` }}
      ).then(res => {
        console.log(res)
        router.push({ name: 'Home'})
      }).catch(err => {
        console.log(err.response)
      })
    }

    const clickDeleteMeeting = function () {
      ElMessageBox.confirm(
        ' ?   .',
        ' ',
        {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning',
        }
      )
        .then(() => {
          store.dispatch('deleteMeeting')
          ElMessage({
            type: 'success',
            message: '.',
          })
          deleteMeeting()
        })
        .catch(() => {
          ElMessage({
            type: 'info',
            message: '.',
          })
        })
    }

    const leaveMeeting = function () {
      axios.delete(`/meeting/${store.state.meeting.id}/users/${store.state.user.id}`, 
        { headers: { Authorization: `Bearer ${localStorage.getItem("token")}` }}
      ).then(res => {
        console.log(res)
        axios.get(`/meeting/${store.state.meeting.id}`).then(res => {
          console.log("  ")
          console.log(res)
        })
      }).catch(err => {
        console.log(err.response)
      }) 
    }

    const clickLeaveMeeting = function () {
      ElMessageBox.confirm(
        ' ?',
        ' ',
        {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning',
        }
      )
        .then(() => {
          router.push({ name: 'Home'})
          leaveMeeting()
          // store.dispatch('deleteMeeting')
          emit('leave')
          ElMessage({
            type: 'success',
            message: '.',
          })
        })
        .catch(() => {
          ElMessage({
            type: 'info',
            message: '.',
          })
        })
    }

    const emitReadySignal = function () {
      emit('ready')
    }

    const startMeeting = function () {
      const numOfParticipants = participants.value.length
      const numOfReady = participants.value.filter((participant: { ready: boolean }) => participant.ready).length
      // console.log(numOfParticipants, numOfReady)
      if (numOfParticipants === numOfReady) {
        emit('start')
      } else {
        ElMessage({
          message: '   .',
          type: 'warning',
        })
      }
    }


    return { participants, clickDeleteMeeting, clickLeaveMeeting, emitReadySignal, startMeeting }
  }
})
